.data{
    border-radius: 5px;
    box-shadow: 5px 5px 10px 1px lightgrey;
    position: relative;
    margin-top: 20px;
    margin-right: 20px;
    cursor: pointer;

    &--size{
        height: 140px;
        width: 300px;
    }

    &--theme{
        background-color: white;
    }

    &__name{
        font-weight: 400;
        font-size: 11px;
        letter-spacing: 2px;
        color: gray;
        margin: 30px 0 15px 0;
    }

    &__count{
        font-weight: 400;
        font-size: 30px;
        color: #3D5170;
    }

    &__percentage{
        font-size: 11px;
    }

    &__row{
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 15px 0;
        color: #17C671;
    }

    &__icon{
        margin-right: 2px;
    }

    &__canvas{
        position: absolute;
        align-self: flex-end;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 100%;
        text-align: end;
        // z-index: ;
    }
}

.users{
    background-color: white;
    border-radius: 10px;

    &__header{
        height: 60px;
        display: flex;
        align-items: center;
    }

    &__title{
        font-weight: 400;
        font-size: 16px;
        margin-left: 10px;
    }

    &__box{
        height: 50px;
        background-color: #FBFBFB;
        border-bottom: 1px solid #E1E5EB;
        border-top: 1px solid #E1E5EB;
    }
}

#users__canvas{
    height: 600px;
    max-width: 1000px;
    background-color: white;
}